@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.app {
  min-height: 100vh;
  width: 100%;
  background: url(/covid-tracker-v1/static/media/Minimalist-Wallpaper-09-1620-x-2880.f2e1403e.jpg);
  background-size: cover;
  background-position: center;
}

main {
  padding: 25px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  min-height: 100vh;
  font-family: "Montserrat", sans-serif;
}

.search-box {
  width: 90%;
  height: 3.5rem;
  background: rgba(255, 255, 255, 0.4);
  border-radius: 2rem;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  padding: 0 0.5rem;
}

.nav-icon {
  height: 1.5rem;
  width: 1.5rem;
}

.search-input {
  -webkit-flex: 1 1;
          flex: 1 1;
  height: 100%;
  margin: 0 0.5rem;
}

.search-input input {
  width: 100%;
  height: 100%;
  background: none;
  outline: none;
  border: none;
}

.ico {
  margin: 0 0.5rem;
}

.container {
  -webkit-flex: 1 1;
          flex: 1 1;
  text-align: center;
  width: 100%;
}

.country-header {
  margin: 1.2rem 0 1rem;
  font-size: 2.5rem;
  font-weight: 600;
}

.current-time {
  font-size: 1.5rem;
  margin: 1rem 0 1.1rem;
}

.tc-header {
  display: inline-block;
  font-size: 2rem;
  font-weight: 500;
  border-bottom: 2px dashed black;
  margin-bottom: 5px;
}

.tc-cases {
  font-size: 2rem;
}

.card {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  width: 100%;
  margin-top: 0.8rem;
}

.card .card-header {
  font-size: 1.5em;
  margin-top: 0.83em;
  margin-bottom: 0.83em;
  margin-left: 0;
  margin-right: 0;
  font-weight: bold;
  margin-bottom: 5px;
}

.card-body {
  height: 3rem;
  width: 60%;
  max-width: 260px;
  font-size: 1.8rem;
  display: grid;
  place-items: center;
  background: rgba(255, 255, 255, 0.6);

  border-bottom-right-radius: 4px;
  border-bottom-left-radius: 4px;
}

.active-cases .card-body {
  box-shadow: 0 5px #fccc0e;
}

.recovered-cases .card-body {
  box-shadow: 0 5px #2ecc71;
}

.death-cases .card-body {
  box-shadow: 0 5px #e74c3c;
}

.footer a {
  color: black;
  text-decoration: none;
  font-weight: bold;
}

@media only screen and (min-width: 600px) {
  .app {
    background: url(/covid-tracker-v1/static/media/wp5418527.e1e5e15e.jpg);
    background-size: cover;
  }
  .search-box {
    width: 40%;
  }

  .country-header {
    font-size: 3.5rem;
  }

  .total-cases {
    margin: 2rem 0;
  }

  .card {
    margin-top: 2.5rem;
  }

  .country-header {
    margin: 2rem 0 1rem;
  }
}

@media screen and (max-height: 710px) {
  main {
    padding: 10px;
  }

  .card {
    margin-top: 0.4rem;
    margin-bottom: 2rem;
  }

  .total-cases div {
    font-size: 1.5rem;
  }

  .search-input {
    margin: 0 0.2rem;
  }
}

